import React from 'react';
import { StyleSheet, TouchableWithoutFeedback, View } from 'react-native';
import Svg, { Circle, Ellipse, G, LinearGradient, RadialGradient, Line, Path, Polygon, Polyline, Rect, Symbol, Text, Use, Defs, Stop } from 'react-native-svg';
import Utils, { Logger } from '../../utils/Utils';

export default class PlayPauseIcon extends React.Component {
  constructor(props) {
    super(props);

    this.state = props.skinState;
    this.uzPlayer = props.player;
  }

  render() {
    const view = (
      <TouchableWithoutFeedback onPress={this._toggleControls.bind(this)}>
        <View
          {...this.props}
          style={styles.view}>
          {
            this.uzPlayer.didJustFinish ?
            <Svg width="48" height="100%" viewBox="-24 -24 96 96">
              <G id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                <G id="icon/replay">
                  <G id="Page-1">
                    <Path d="M42.2487373,31.1237373 L29.1237373,31.1237373 L34.0295479,36.0295479 C31.5504952,38.5084639 28.2545479,39.8737373 24.7487373,39.8737373 C21.2429268,39.8737373 17.9469795,38.5084639 15.4679268,36.0295479 C12.9890108,33.5504952 11.6237373,30.2545479 11.6237373,26.7487373 C11.6237373,23.2429268 12.9890108,19.9469795 15.4679268,17.4679268 C17.9469795,14.9890108 21.2429268,13.6237373 24.7487373,13.6237373 C28.2545479,13.6237373 31.5504952,14.9890108 34.0294795,17.4679952 C34.2364717,17.674919 34.4348506,17.8881319 34.6261202,18.1060616 L37.9183077,15.2253291 C34.7106807,11.5624287 30.0001045,9.24873734 24.7487373,9.24873734 C15.0837471,9.24873734 7.24873734,17.0837471 7.24873734,26.7487373 C7.24873734,36.4137276 15.0837471,44.2487373 24.7487373,44.2487373 C29.5812666,44.2487373 33.9556514,42.2893526 37.1223311,39.1223311 L42.2487373,44.2487373 L42.2487373,31.1237373 Z" id="Fill-2" fill="#FFFFFF" fill-rule="nonzero" transform="translate(24.748737, 26.748737) rotate(-135.000000) translate(-24.748737, -26.748737) "></Path>
                  </G>
                </G>
              </G>
            </Svg> : 
            this.state.playbackStatus && this.state.playbackStatus.isPlaying ?
            <Svg width="48" height="100%" viewBox="-24 -24 96 96">
              <G id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                <G id="icon/play">
                  <G id="Page-1">
                    <Path d="M10,39.9999989 L20,39.9999989 L20,8 L10,8 L10,39.9999989 Z M28,8 L28,39.9999989 L38,39.9999989 L38,8 L28,8 Z" id="Fill-2" fill="#FFFFFF"></Path>
                  </G>
                </G>
              </G>
            </Svg> : 
            <Svg width="100%" height="100%" viewBox="-36 -24 96 96">
              <G id="Page-1" stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                <G id="paused" transform="translate(-1.000000, 0.000000)" fill="#FFFFFF" fill-rule="nonzero">
                  <Polygon id="Fill-2" points="1 47.1111121 33.7407415 24.0000005 1 0.888888893"></Polygon>
                </G>
              </G>
            </Svg>
          }
        </View>
      </TouchableWithoutFeedback>
    )

    return view;
  }

  _toggleControls() {
    if (this.uzPlayer.didJustFinish) {
      this.uzPlayer.play(0);
    } else {
      this.uzPlayer.togglePlayPause();
    }
  }
}

const styles = StyleSheet.create({
  view: {
    alignSelf: 'flex-end',
    width: 48,
    height: 48
  }
});
